<template>
  <main class="main">
    <nav aria-label="breadcrumb">
      <ol class="breadcrumb c-breadcrumb-bg">
        <li class="breadcrumb-item"><router-link :to="{ name: 'Shop'}" >首頁</router-link></li>
        <li class="breadcrumb-item active" aria-current="page">影片介紹</li>
      </ol>
    </nav>
    <div class="container mt-4">
      <div class="row justify-content-center">
        <div class="col-md-6">
          <img :src="product.imageUrl" class="c-img-fit" :alt="product.title">
        </div>
        <div class="col-md-6 my-4">
          <h1 class="h2">{{ product.title }}</h1>
          <hr>
          <span class="badge badge-info">
            {{ product.category }}
          </span>
          <p class="mt-2">{{ product.description }}</p>
          <div class="my-3" v-if="product.price">
            <del class="c-origin-price">{{ product.origin_price | currency }}</del>
            <p class="h4 c-price"> {{ product.price | currency }} </p>
          </div>
          <div class="input-group">
            <div class="row">
              <div class="col-6 mb-3">
                <select name="" class="form-control" v-model="product.num">
                  <!-- <option value="0" selected disabled>請選擇訂閱期限</option> -->
                  <option :value="num" v-for="num in 10" :key="num">
                    訂閱 {{num}} {{product.unit}}
                  </option>
                </select>
              </div>
              <div class="col-6">
                <button class="btn btn-warning btn-block"
                  @click.prevent="addtoCart(product.num)">
                  <i class="fa fa-cart-plus" aria-hidden="true"></i> 加入購物車
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row justify-content-center my-4">
        <div class="col-md-10">
          <h2 class="text-center">劇情簡介</h2>
          <p>{{ product.content }}</p>
          <div class="my-4">
            <router-link class="btn btn-info"
              :to="{ name: 'JapanTv'}" v-if="product.category == '經典日劇'">
              &lt;&lt; 回經典日劇
            </router-link>
            <router-link class="btn btn-info"
              :to="{ name: 'KoreaTv'}" v-else>
              &lt;&lt; 回口碑韓劇
            </router-link>
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'Product',
  props: ['productId'],
  data() {
    return {
      num: 0,
    };
  },
  created() {
    this.$store.dispatch('getProduct', this.productId);
  },
  computed: {
    ...mapState(['product', 'cartData']),
  },
  methods: {
    addtoCart(productNum) {
      const vm = this;
      const alreadyCarts = vm.cartData.carts.filter((item) => item.product_id === vm.productId);
      if (alreadyCarts.length > 0) {
        vm.num = productNum + alreadyCarts[0].qty;
        vm.removeCartItem(alreadyCarts[0], vm.addtoCartService);
      } else {
        vm.num = productNum;
        vm.addtoCartService();
      }
    },
    removeCartItem(carts, cb) {
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${carts.id}`;
      this.axios.delete(url).then(() => {
        cb();
      });
    },
    addtoCartService() {
      const vm = this;
      const id = vm.productId;
      const nums = vm.num;
      vm.$store.dispatch('addtoCart', { id, nums });
    },
  },
};
</script>
